import { useEffect, useState } from "react";
import axios from "axios";

const BookDisplay = () => {
  const [booksData, setBooksData] = useState([]);

  useEffect(() => {
    axios.get('http://localhost:8800/api/book/getbook/')
      .then((response) => {
        setBooksData(response.data);
      })
      .catch((error) => {
        console.error("Error fetching books:", error);
      });
  }, []);

  return (
    <section className="text-gray-600 body-font">
      <div className="container px-5 py-24 mx-auto">
        <div className="flex flex-wrap -m-4">
          {booksData.map((book) => (
            <div key={book.bookName} className="lg:w-1/4 md:w-1/2 p-4 w-full">
              <a className="block relative h-48 rounded overflow-hidden">
                <img
                  alt={book.bookName}
                  className="object-cover object-center w-full h-full block"
                  src={book.bookImage}
                />
              </a>
              <div className="mt-4">
                <h3 className="text-gray-500 text-xs tracking-widest title-font mb-1">{book.bookCategory}</h3>
                <h2 className="text-gray-900 title-font text-lg font-medium">{book.bookName}</h2>
                <p className="mt-1">${book.bookCost}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default BookDisplay;
